import { PageTitle } from "../components/components.js";
import { containerStyle } from "./styles.js";
import { MachinesPage } from "../assets/assets.js";
import Certificate from "../components/Makineler/Makineler.jsx";
import { Helmet } from "react-helmet-async";

const Makineler = () => {
  return (
    <>
      <Helmet>
        <title>Ankara Mini Kepçe Kiralama | İlhan Aydos Hafriyat</title>
        <meta
          name="description"
          content="Mini kepçe, ekskavatör, bobcat ve iş makineleri kiralama hizmetleri. Ankara genelinde hızlı ve güvenilir hizmet."
        />
        <link rel="canonical" href="https://ankaramakinekiralama.com/makineler" />
      </Helmet>

      <div id="Makineler" className={`${containerStyle}`}>
        <PageTitle title={"Makinelerimiz"} />

        {/* Featured Machine */}
        <div className="container-industrial-light mt-10 p-6">
          <h3 className="text-4xl text-yellow-400 font-bold">⚡ En Çok Tercih Edilen Makinemiz: Mini Ekskavatör</h3>
          <p className="text-zinc-200 mt-3 text-sm">
            Peyzaj, drenaj, iç mekan yıkım işlerinde yüksek verimlilik sağlar. Kompakt yapısıyla dar alanlara kolayca girer.
          </p>
        </div>

        {/* Machine Comparison Table */}
        <div className="overflow-x-auto rounded-xl mb-6">
          <table className="w-full text-left text-sm border-collapse bg-[#1f1f1f] text-white shadow-xl">
            <thead>
              <tr className="bg-yellow-500 text-black">
                <th className="py-2 px-4">Makine</th>
                <th className="py-2 px-4">Yükseklik</th>
                <th className="py-2 px-4">Genişlik</th>
                <th className="py-2 px-4">Kullanım Alanı</th>
              </tr>
            </thead>
            <tbody>
              <tr className="border-t border-zinc-600 hover:bg-[#2b2b2b]">
                <td className="py-2 px-4">Mini Ekskavatör</td>
                <td className="py-2 px-4">241cm</td>
                <td className="py-2 px-4">130cm</td>
                <td className="py-2 px-4">Peyzaj / Dar Alanlar</td>
              </tr>
              <tr className="border-t border-zinc-600 hover:bg-[#2b2b2b]">
                <td className="py-2 px-4">3.5 Ton Ekskavatör</td>
                <td className="py-2 px-4">251cm</td>
                <td className="py-2 px-4">170cm</td>
                <td className="py-2 px-4">Kanal / Yıkım</td>
              </tr>
              <tr className="border-t border-zinc-600 hover:bg-[#2b2b2b]">
                <td className="py-2 px-4">Bobcat</td>
                <td className="py-2 px-4">215cm</td>
                <td className="py-2 px-4">185cm</td>
                <td className="py-2 px-4">Kanal / Dar Alanlar</td>
              </tr>
              <tr className="border-t border-zinc-600 hover:bg-[#2b2b2b]">
                <td className="py-2 px-4">Büyük Ekskavatör</td>
                <td className="py-2 px-4">251cm</td>
                <td className="py-2 px-4">185cm</td>
                <td className="py-2 px-4">Hafriyat / Bina Yıkım</td>
              </tr>
            </tbody>
          </table>
        </div>

        {/* Machines Scroll Section */}
        <div className="container-industrial-light mt-10 p-6">
          <div className="flex gap-4 flex-nowrap overflow-x-auto overflow-y-hidden custom-scrollbar">
            {MachinesPage.map(
              ({ img, title, description, issuedBy, credentialURL }, index) => (
                <Certificate
                  key={index}
                  isFlexShrink={true}
                  img={img}
                  title={title}
                  description={description}
                  issuedBy={issuedBy}
                  credentialURL={credentialURL}
                />
              )
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Makineler;
